import React from 'react';
import { Link } from 'gatsby';
import LogoImage from './Naeri_Love_Lee_Logo-3.svg';

import * as styles from './logo.module.css';

const Logo = () => (
    <h1 className={styles.logo}>
        <Link to="/" className={styles.logoLink}>
            <img src={LogoImage} alt="NaeriLove Lee" />
        </Link>
    </h1>
);

<img src="data:image/svg+xml,%3csvg.." alt="LogoImage" />

export default Logo;